.Choices {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
}

.Choices .choice {
    -ms-flex: 1 1;
        flex: 1 1;
    cursor: pointer;
    padding: 4px;
    border: 1px solid gray;
    text-align: center;
    min-width: 1.5rem;
}
.Choices .choice:hover {
    background-color: #b7edb9;
}
.Choices .choice.selected {
    color: white;
    background-color: #08940d;
}



