.Order {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
}

.Order .search-bar {
    border-bottom: 1px solid black;
}

.Order .search-bar {
    font-size: 24px;
}

.Order .search-bar input {
    border: none;
    padding: 8px;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.Order .search-bar .close {
    cursor: pointer;
    font-size: 32px;
    -ms-flex-item-align: center;
        align-self: center;
    margin-right: 8px;
    text-align: center;
}

.Order .search-results .result {
    padding: 8px;
    border-bottom: 1px solid gray;
    cursor: pointer;
}
.Order .search-results .result:hover {
    background-color: #b7edb9;
}

.Order .search-results .result .name {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.Order .search-results .result .add {
    font-size: 18px;
}



